body {
  color: #333;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Georgia, serif;
  line-height: 1.6;
}

h1, h2 {
  text-align: center;
  font-family: Times New Roman, Times, serif;
}

p {
  text-align: justify;
  font-size: 16px;
}

header {
  text-align: center;
  background-color: #fafafa;
